/*Navigation bar and links*/
.linkStyle{
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  border-right: 1px solid #bbb;
}

.linkStyle:hover {
  background-color: #111;
}

.nav{
  overflow: hidden;
  background-color: #092327;
}

.active {
  background-color: #0B5351;
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  border-right: 1px solid #bbb;
}
/**********************************/


.me-img{
  border-radius: 35px;
  padding: 20px; 
  width: 400px;
  height: 400px; 
}
  

/*Containers*/
.profile-container{
  background-color: #ffffff;
  margin: auto;
  width: 90%;
  }




/*Paragraph*/
.paragraph {
  margin: auto;
  width: 50%;
  border: 3px solid #00A9A5;
  padding: 10px;
}

/*Projects*/

.resume{
  text-align: center;
  width: 200px;
  height: 120px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}


.resume img {
  width: 100%;
  height: auto;
}



.first-web{
  margin: auto;
  width: 90%;
  /*border: 3px solid #00A9A5;*/
  padding: 10px;
}

.text-block {
  border-radius: 15px;
  bottom: 20px;
  right: 20px;
  background-color: #4E8098;
  color: white;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 30px;
  padding-right: 30px;
  
 
}


.contact-block{
  text-align: center;
  width: 200px;
  height: 120px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
